    width: 113px;
  }
}
#review .review__profile__rank {
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 150%;
}
@media (min-width: 1400px) {
  #review .review__profile__rank {
    font-size: 1.25rem;
    line-height: 160%;
  }
}
#review .review__text {
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 150%;
  font-weight: 300;
  color: #080809;
}
@media (min-width: 1400px) {
  #review .review__text {
    font-size: 1.25rem;
    line-height: 160%;
  }
}
#review .review__text p:last-child {
  margin-bottom: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #FAFCFF url("/img/ellipse-inner.svg") no-repeat;
  background-position: 100% 0;
}